<template>
  <div>
    <Loader
      v-show="!lid"
      v-if="loading || domain.checking || ttslogin.loading || auth.checking"
    />

    <md-dialog-confirm
      :md-active="!loading && !addUser && !lid && alredyLogIn"
      md-title="Добавить аккаунт?"
      md-content="На этом устройстве уже был выполнен вход.<br/>Для открытия приложения нажмите <b>Отмена</b>"
      md-confirm-text="Добавить"
      md-cancel-text="Отмена"
      @md-cancel="$router.replace('/diary')"
      @md-confirm="addUser = true"
    />

    <md-dialog :md-active="!!lid" class="login" :md-fullscreen="false">
      <Loader v-if="loading" />
      <md-dialog-title>Добавить пользовятеля?</md-dialog-title>
      <UserInfo
        :id="lid"
        :cache="false"
        @error="
          e => {
            if (e != null) return;
            alert = 'Ссылка неактивна';
            lid = false;
          }
        "
      />
      <md-dialog-actions>
        <md-button
          class="md-primary"
          :disabled="loading"
          @click="$router.replace('/diary')"
          >Отмена</md-button
        >
        <md-button class="md-primary" @click="checkLink" :disabled="loading"
          >Добавить</md-button
        >
      </md-dialog-actions>
    </md-dialog>

    <md-steppers
      md-linear
      md-vertical
      :md-active-step.sync="step"
      v-if="!loading && !lid && addUser"
    >
      <md-step
        id="Domain"
        md-label="Домен"
        :md-editable="false"
        :md-description="domain.checking ? 'Проверка...' : null"
        :md-done="domain.checked === true && domain.suitable === true"
        :md-error="
          domain.checked === true && domain.suitable === false
            ? 'Сайт не подходит'
            : ''
        "
      >
        <p>Ниже нужно указать ссылку на ваш сетевой город.</p>
        <i>
          Например:
          <a href="//sgo.rso23.ru/" target="_blank">https://sgo.rso23.ru/</a>
          или
          <a href="//212.220.99.65/" target="_blank">http://212.220.99.65/</a>
        </i>
        <md-field
          class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
        >
          <md-icon>link</md-icon>
          <label>Ссылка</label>
          <md-input autocomplete="off" v-model.trim="domain[0]"></md-input>
        </md-field>
        <md-button
          class="md-primary"
          @click="checkDomain"
          :disabled="domain.checking"
          >{{ domain.checking ? "Проверка..." : "Продолжить" }}</md-button
        >
      </md-step>

      <md-step
        id="TTSLogin"
        :md-editable="!auth.checking"
        :md-done="ttslogin[0].length > 0"
        md-label="Образовательная организация"
        :md-description="ttslogin.loading ? 'Загрузка формы...' : null"
      >
        <p v-if="ttslogin.selectors.length">
          Ниже вам нужно выбрать ваше учебное заведение (как в сетевом)
        </p>
        <md-field
          v-for="(selector, index) in ttslogin.selectors"
          :key="selector.id"
          :class="validationSelect(selector.name)"
          class="md-layout-item md-xsmall-size-100 md-small-size-70 md-medium-size-50 md-large-size-40 md-xlarge-size-30"
        >
          <label :for="selector.id">{{ ttslogin.labels[selector.id] }}</label>
          <md-select
            :id="selector.id"
            :name="selector.name"
            v-model="selector.value"
            @md-selected="loadItems(index)"
          >
            <md-option
              v-for="option in selector.options"
              :key="option.id"
              :value="option.id"
              >{{ option.name }}</md-option
            >
          </md-select>
          <span
            class="md-helper-text"
            v-if="selector.options.length > 0 && !checkSelector(selector.name)"
            >Выберите {{ ttslogin.labelsInfo[selector.id] }}</span
          >
          <span class="md-error"
            >Вы забыли выбрать {{ ttslogin.labelsInfo[selector.id] }}</span
          >
        </md-field>
        <md-button
          class="md-primary"
          @click="makeTTSLogin"
          :disabled="ttslogin.loading"
          v-if="ttslogin.selectors.length"
          >{{ ttslogin.loading ? "Загрузка..." : "Продолжить" }}</md-button
        >
      </md-step>

      <md-step id="Authorization" md-label="Авторизация">
        <p>
          Ниже нужно ввести логин и пароль для входа в сетевой город.
        </p>
        <i
          >Эти данные хранятся на нашем сервере и не передаются третьим
          лицам.</i
        >
        <md-field
          :class="validationAuth('login')"
          class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
        >
          <label>Логин</label>
          <md-input autocomplete="login" v-model="auth.login"></md-input>
          <span class="md-error" v-if="$v.auth && !$v.auth.login.required"
            >Вы забыли ввести логин</span
          >
          <span
            class="md-error"
            v-else-if="
              auth.checked &&
                !auth.suitable &&
                $v.auth &&
                !$v.auth.login.suitable
            "
            >Логин введен неверно</span
          >
        </md-field>
        <md-field
          :class="validationAuth('password')"
          class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
        >
          <label>Пароль</label>
          <md-input
            type="password"
            autocomplete="password"
            v-model="auth.password"
          ></md-input>
          <span class="md-error" v-if="$v.auth && !$v.auth.password.required"
            >Вы забыли ввести пароль</span
          >
          <span
            class="md-error"
            v-else-if="
              auth.checked &&
                !auth.suitable &&
                $v.auth &&
                !$v.auth.password.suitable
            "
            >Пароль введен неверно</span
          >
        </md-field>
        <md-button
          class="md-primary"
          @click="checkAuth"
          :disabled="auth.checking"
          >{{ auth.checking ? "Проверка..." : "Войти" }}</md-button
        >
      </md-step>
    </md-steppers>

    <md-snackbar
      md-position="center"
      :md-active="!!alert"
      @update:mdActive="close => (!close ? (alert = false) : '')"
    >
      <span>{{ alert }}</span>
    </md-snackbar>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import required from "vuelidate/lib/validators/required";
import Loader from "@/components/Loader";
import UserInfo from "@/components/UserInfo";

export default {
  data: () => ({
    lid: null,
    alert: false,
    loading: true,
    addUser: false,
    alredyLogIn: false,
    step: "Domain",
    domain: {
      0: "",
      checked: false,
      checking: false,
      suitable: false
    },
    ttslogin: {
      0: "",
      labels: {
        countries: "Страна",
        states: "Регион",
        provinces: "Округ/Район",
        cities: "Населённый пункт",
        funcs: "Тип ОО",
        schools: "Образовательная организация"
      },
      labelsInfo: {
        countries: "cтрану",
        states: "регион",
        provinces: "округ/район",
        cities: "населённый пункт",
        funcs: "тип ОО",
        schools: "образовательную организацию"
      },
      selectors: [],
      loading: false
    },
    auth: {
      login: "",
      password: "",
      checked: false,
      checking: false,
      suitable: false
    }
  }),
  computed: {
    ...mapState({
      connectedUsers: state => state.settings.storage.connectedUsers
    }),
    uid: {
      get() {
        return this.$store.state.settings.storage.uid;
      },
      set(val) {
        this.$store.state.settings.storage.uid = val;
        return true;
      }
    }
  },
  methods: {
    ...mapActions([
      "CheckHost",
      "AuthForm",
      "SelectItems",
      "AddUser",
      "AddUserViaLink"
    ]),
    validationSelect(fieldName) {
      const field = this.$v[fieldName];
      const selector = this.ttslogin.selectors.find(s => s.name == fieldName);
      if (field) {
        return {
          "md-invalid":
            field.$invalid && field.$dirty && selector?.options?.length
        };
      }
    },
    validationAuth(fieldName) {
      const field = this.$v.auth?.[fieldName];
      if (field) {
        return {
          "md-invalid":
            (field.$invalid && field.$dirty) ||
            field.$model === this.auth[`old_${fieldName}`]
        };
      }
    },
    checkSelector(name) {
      const selector = this.ttslogin.selectors.find(s => s.name == name);
      if (!selector) return false;
      return !!selector.options.find(o => o.id == selector.value);
    },
    checkDomain() {
      this.domain.checked = false;
      this.domain.checking = true;
      this.domain.suitable = false;
      this.domain[0] = this.domain[0].replace(/https*:\/\/|\/.*/g, "");

      this.CheckHost(this.domain[0])
        .then(suitable => {
          if (suitable) {
            this.loadForm();
            this.step = "TTSLogin";
          }
          this.domain.checked = true;
          this.domain.suitable = suitable;
        })
        .catch(err => {
          console.error(err);
          this.domain.checked = false;
          this.alert = "Произошла ошибка, попробуйте ещё раз";
        })
        .finally(() => (this.domain.checking = false));
    },
    loadForm() {
      this.$v.$reset();
      this.ttslogin[0] = "";
      this.ttslogin.loading = true;
      this.ttslogin.selectors = [];
      this.AuthForm(this.domain[0])
        .then(selectors => (this.ttslogin.selectors = selectors))
        .catch(err => {
          this.loadForm();
          console.error(err);
        })
        .finally(() => (this.ttslogin.loading = false));
    },
    loadItems(index) {
      if (
        this.ttslogin.selectors.length == index + 1 ||
        this.ttslogin.selectors[index].value == null
      )
        return;
      this.$v.$reset();
      this.ttslogin[0] = "";
      this.ttslogin.loading = true;

      const name = this.ttslogin.selectors[index].name;
      const selected = {};
      for (let i = 0; i <= index; i++) {
        const selector = this.ttslogin.selectors[i];
        selected[selector.name] = selector.value;
      }
      for (let i = index + 1; i < this.ttslogin.selectors.length; i++) {
        this.ttslogin.selectors[i].value = null;
        this.ttslogin.selectors[i].options = [];
      }
      this.SelectItems({
        host: this.domain[0],
        lastElem: name,
        selected
      })
        .then(items => (this.ttslogin.selectors[index + 1].options = items))
        .catch(err => {
          console.error(err);
          this.loadItems(index);
        })
        .finally(() => (this.ttslogin.loading = false));
    },
    makeTTSLogin() {
      this.$v.$touch();
      if (this.$v.$invalid) return;
      for (const selector of this.ttslogin.selectors) {
        if (this.ttslogin[0]) this.ttslogin[0] += "&";
        this.ttslogin[0] += selector.name + "=" + selector.value;
      }
      this.step = "Authorization";
      this.$v.$reset();
    },
    checkAuth() {
      this.$v.$touch();
      if (this.$v.$invalid) return;

      this.auth.checking = true;
      this.auth.suitable = false;
      this.auth.old_login = this.auth.old_password = null;
      this.AddUser({
        host: this.domain[0],
        ttsLogin: this.ttslogin[0],
        login: this.auth.login,
        password: this.auth.password
      })
        .then(id => {
          this.uid = id;
          this.connectedUsers.push(id);
          this.$router.replace("/diary");
        })
        .catch(err => {
          this.auth.checked = true;
          switch (err.code) {
            case 4007:
              this.auth.suitable = false;
              break;
            case 4005:
              this.auth.checked = false;
              this.alert = "Сервер не получил требуемых данных";
              break;
            case 4006:
              this.auth.checked = false;
              this.alert = "Ошибка парсера, попробуй ещё раз";
              break;
            case 4008:
              this.auth.checked = false;
              this.alert =
                this.domain[0] + " не отвечает, попробуйте войти позже";
              break;
            default:
              this.auth.checked = false;
              this.alert = "Неизвестная ошибка, попробуйте ещё раз";
          }
          console.error(err);
        })
        .finally(() => {
          this.auth.checking = false;
          this.auth.old_login = this.auth.login;
          this.auth.old_password = this.auth.password;
        });
    },
    checkLink() {
      this.loading = true;
      this.AddUserViaLink(this.lid)
        .then(id => {
          this.uid = id;
          this.connectedUsers.push(id);
          this.$router.replace("/diary");
        })
        .catch(err => {
          this.lid = false;
          this.alert = "Ссылка неактивна";
          console.error(err);
        });
    }
  },
  validations() {
    const selectors = {};
    for (const selector of this.ttslogin.selectors) {
      selectors[selector.name] = {
        exist: () => this.checkSelector(selector.name)
      };
    }
    return this.step == "Authorization"
      ? {
          auth: {
            login: { required },
            password: { required }
          }
        }
      : selectors;
  },
  mounted() {
    this.loading = true;
    this.lid = this.$route.params.lid;
    this.addUser = !!this.$route.params.addUser;
    this.$root.$children[0]
      .checkExist()
      .then(exist => {
        if (!exist) this.addUser = true;
        this.alredyLogIn = exist;
      })
      .finally(() => (this.loading = false));
  },
  components: {
    Loader,
    UserInfo
  }
};
</script>

<style>
div.login div.user-info {
  padding: 0 24px;
  flex-direction: row !important;
  justify-content: center !important;
}
div.login div.user-info > img {
  width: 60px !important;
  height: 60px !important;
  margin-left: 0 !important;
  margin-right: 12px !important;
}
div.login div.user-info > div {
  align-items: flex-start !important;
}
div.login div.user-info > div > span.md-body-1 {
  font-size: 16px !important;
}
div.login div.user-info > div > span.md-caption {
  font-size: 14px !important;
}
</style>
